<template>
  <div>
    <div class="row">
      <div class="col-md-5 border">
        <div>
          <video controls id="my-video" autoplay>
            <source :src="video" type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        </div>
        <div>
          <button @click="takeScreenshot" class="btn btn-primary m-2">Take Screen shot</button>
        </div>
      </div>

      <div class="col-md-5 border">
        <div>
          <canvas
            ref="can"
            class="img-fluid border"
            id="my-canvas"
            :height="videoHeight"
            :width="videoWidth"
          ></canvas>
        </div>

        <div>
          <button type="button" class="btn btn-primary m-2" @click="addLogo">Add Logo</button> &nbsp;&nbsp;
          <button type="button" class="btn btn-primary m-2" @click="addLine">Add Line</button> &nbsp;&nbsp;
          <button type="button" class="btn btn-primary m-2" @click="addCircle">Add Circle</button>
          &nbsp;&nbsp;
          <button type="button" class="btn btn-primary m-2" @click="addAcuteAngle">Add Acute Angle</button>
          &nbsp;&nbsp;
          <button type="button" class="btn btn-primary m-2" @click="addAngle90">Add Angle 90</button>
          <br />
          <button type="button" class="btn btn-primary m-2" @click="clear">clear</button>
          &nbsp;&nbsp;
          <button type="button" class="btn btn-primary m-2" @click="save">save</button>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-md-6">
        <img src="" id="screenshot_preview" />
      </div>
    </div>
  </div>
</template>

<script>
import { fabric } from "fabric";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
import videoTest from "./assets/videoTest.mp4";

import line from "./assets/line.png";
import acute_angle from "./assets/acute_angle.png";
import circle from "./assets/circle.png";
import angle_90 from "./assets/angle_90.png";
import logo from "./assets/logo.png";

export default {
  name: "App",
  components: {},
  data() {
    return {
      video: "",
      canvas: null,
      videoHeight: 400,
      videoWidth: 700,
    };
  },
  mounted() {
    this.video = videoTest;
    const ref = this.$refs.can;
    this.canvas = new fabric.Canvas(ref);
  },

  methods: {
    takeScreenshot() {
      const video = document.getElementById("my-video");

      const tmpCanvas = document.createElement("canvas");
      // scale the canvas accordingly

      tmpCanvas.width = video.videoWidth;
      tmpCanvas.height = video.videoHeight;
      // draw the video at that frame
      tmpCanvas
        .getContext("2d")
        .drawImage(video, 0, 0, tmpCanvas.width, tmpCanvas.height);
      // convert it to a usable data URL
      const dataURL = tmpCanvas.toDataURL();
      // create a rectangle object
      const canvas = this.canvas;
      canvas.clear();
      fabric.Image.fromURL(dataURL, function (oImg) {
        oImg.scale(1);
        canvas.add(oImg);
      });
    },

    addLine() {
      const canvas = this.canvas;
      fabric.Image.fromURL(line, function (oImg) {
        oImg.scale(0.2);
        canvas.add(oImg);
      });
    },

    addCircle() {
      const canvas = this.canvas;
      fabric.Image.fromURL(circle, function (oImg) {
        oImg.scale(0.2);
        canvas.add(oImg);
      });
    },
    addAcuteAngle() {
      const canvas = this.canvas;
      fabric.Image.fromURL(acute_angle, function (oImg) {
        oImg.scale(0.2);
        canvas.add(oImg);
      });
    },
    addAngle90() {
      const canvas = this.canvas;
      fabric.Image.fromURL(angle_90, function (oImg) {
        oImg.scale(0.2);
        canvas.add(oImg);
      });
    },

    addLogo() {
      const canvas = this.canvas;
      fabric.Image.fromURL(logo, function (oImg) {
        oImg.scale(0.2);
        canvas.add(oImg);
      });
    },

    save() {
      const canvas = this.canvas;
      const svg = canvas.toSVG();
      var myWindow = window.open("", "", "width=1000,height=500");
      myWindow.document.write(svg);
      myWindow.focus();

      console.log(svg);
    },

    clear() {
      const canvas = this.canvas;
      canvas.clear();
    },
  },
};
</script>

<style >
.border {
  border: 5px solid black;
}
</style>
